body{
    background-color: #000000;
  }
  .App{
    background-color: #000000;
    min-height: 500px;
    width: 400px;
    margin: 40px auto;
    border: 2px solid #fff;
    border-radius: 7%;
  }
  
  #to-do-form input{
    background-color: rgb(27, 112, 137);
    width: 200px;
    height: 50px;
    border: 1px solid #fff;
    padding: 0 20px;
    margin: 20px;
    font-size: 18px;
    border-radius: 10px;
    color: white;
  }
  
  #to-do-form input::placeholder{
    color: rgba(255, 255, 255, 0.5);
  }
  
  #to-do-form input:focus, #to-do-form button:focus{
    outline: none;
    border: 1px solid #a80d0dab;
  }
  
  #to-do-form button{
    height: 50px;
    width: 80px;
    border-radius: 5px;
    border: 1px solid #fff;
    background-color: rgb(202, 156, 24);
    cursor: pointer;
    font-weight: bolder;
    color: white;
    
  }